import React from 'react';
import Indicator from './Indicator';

const EachSkill = ({skill, show, hideSkill}) => {
    const showSkills = skill.length ? (
        skill.map((s, idx) => {
            return (
                <div key={idx + 1} className="container-wrapper each-skill no-margin no-padding margin-left-2">
                    <span className="skill-name">{s.name}</span>
                    <Indicator level={s.level}/>
                </div>
            );
        })
    ) : (
        <span></span>
    )

    if (skill.length && show) {
        return(
            <div className="container-wrapper no-border">
                {showSkills}
            </div>
        )
    } else {
        return(
            ""
        )
    }
}

export default EachSkill