import React, { Component } from 'react';

class ContactForm extends Component {
    state = { 
        firstName: "",
        lastName: "",
        emailAddress: "",
        message: "",
    }

    changeText = (e) => {
        this.setState({[e.target.id]: e.target.value});
    }

    render() { 
        return ( 
            <form className="contact-form justify align margin-bottom" onSubmit={(e) => this.props.submit(e, this.state)}>
                <div>
                    <input type="text" id="firstName" className="text-input name-input margin-top" placeholder="first name" onChange={e => this.changeText(e)} value={this.state.firstName}/>
                    <input type="text" id="lastName" className="text-input name-input margin-top margin-left" placeholder="last name"  onChange={e => this.changeText(e)} value={this.state.lastName}/>
                </div>
                <input type="text" id="emailAddress" className="text-input email-input margin-top--" placeholder="email address" onChange={e => this.changeText(e)} value={this.state.emailAddress}/>
                <textarea id="message" className="text-input message-input margin-top--" placeholder="message" onChange={e => this.changeText(e)} value={this.state.message}/>
                <input type="submit" className="submit-button button-orange margin-top" value="SUBMIT"/>
            </form>
        );
    }
}
 
export default ContactForm